import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { NewsPageComponent } from './components/news-page/news-page.component';
import { NewsCardPageComponent } from './components/news-card-page/news-card-page.component';
import { NewsRubricsResolver } from 'src/app/services/news-rubrics/news-rubrics.resolver';
import { NewsResolver } from 'src/app/services/news/news.resolver';
import { CurrentUserIdResolver } from 'src/app/services/security/current-user-id.resolver';
import { NewsCardResolver } from 'src/app/services/news/news-card.resolver';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';

const routes: Routes = [
  {
    path: '',
    component: NewsPageComponent,
    resolve: { news: NewsResolver, newsRubrics: NewsRubricsResolver, userId: CurrentUserIdResolver },
    data: { animation: 'fadeAnimation', title: 'Новости' }
  },
  {
    path: ':id',
    component: NewsCardPageComponent,
    resolve: { newsCard: NewsCardResolver, userId: CurrentUserIdResolver, groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Новости' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [NewsResolver, NewsRubricsResolver, CurrentUserIdResolver, GroupsResolver, NewsCardResolver],
    exports: [RouterModule]
})
export class NewsRoutingModule { }
