import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-news-card-gallery',
  templateUrl: './news-card-gallery.component.html',
  styleUrls: ['./news-card-gallery.component.scss']
})
export class NewsCardGalleryComponent implements OnInit {

  @Input() items: string[] = [];

  constructor() { }

  ngOnInit() {
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)') || null;
  }
}
