import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { NewsRubric } from './news-rubric';
import { NewsRubricsService } from './news-rubrics.service';
import { Observable } from 'rxjs';

@Injectable()
export class NewsRubricsResolver implements Resolve<NewsRubric[]> {

  constructor(private newsRubricsService: NewsRubricsService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<NewsRubric[]> {
    return this.newsRubricsService.getItems();
  }
}
