import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { News } from 'src/app/services/news/news';
import { NewsComment } from 'src/app/services/news-comments/newsComment';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { NewsService } from 'src/app/services/news/news.service';
import { NewsCommentsService } from 'src/app/services/news-comments/news-comments.service';
import { NewsViewsService } from 'src/app/services/news-views/news-views.service';
import { groups } from 'src/app/constants/groups';
import { NewsView } from 'src/app/services/news-views/newsView';

@Component({
  selector: 'app-news-card-page',
  templateUrl: './news-card-page.component.html',
  styleUrls: ['./news-card-page.component.scss']
})
export class NewsCardPageComponent implements OnInit {
  public item: News;
  public comments: NewsComment[];
  public userId: number;
  public isAdmin = false;

  constructor(
    private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private newsService: NewsService,
    private newsCommentsService: NewsCommentsService,
    private newsViewsService: NewsViewsService
  ) {
    this.sidebarService.handleSetSettings({showSidebar: false, bgColor: '#fff'});
  }

  ngOnInit() {
    this.item = this.route.snapshot.data.newsCard;
    this.userId = this.route.snapshot.data.userId;
    const currentGroups: string[] = this.route.snapshot.data.groups;
    if (currentGroups.indexOf(groups.administrator) >= 0) {
      this.isAdmin = true;
    }

    this.getComments(this.item.id);

    this.newsViewsService.getItems({filter: `slLookupNews/Id eq ${this.item.id}`})
      .subscribe((views: NewsView[]) => {
        if (views && views.length > 0) {
          this.item.viewsCount = views[0].count;
          if (views[0].userIds.every(id => id !== this.userId)) {
            this.newsViewsService.addLike({id: views[0].id});
          }
        } else {
          this.newsViewsService.createItem({
            id: undefined,
            title: this.item.title,
            newsLookupId: this.item.id})
            .subscribe(view => this.newsViewsService.addLike({id: view.id}));
        }
      });
  }

  createComment(comment: string) {
    this.newsCommentsService.createItem({
      id: undefined,
      title: this.item.title,
      newsLookupId: this.item.id,
      comment: comment
    }).subscribe(() => this.getComments(this.item.id));
  }

  editComment(comment: [number, string]) {
    this.newsCommentsService.updateItem({
      id: comment[0],
      title: this.item.title,
      newsLookupId: this.item.id,
      comment: comment[1]
    }).subscribe(() => this.getComments(this.item.id));
  }

  deleteComment(id: number) {
    this.newsCommentsService.deleteItem({ id: id, title: ''})
      .subscribe(() => this.getComments(this.item.id));
  }

  private getComments(newsId: number): void {
    this.newsCommentsService.getItems({filter: `slLookupNews/Id eq ${this.item.id}`})
      .subscribe((comments: NewsComment[]) => this.comments = comments);
  }

  public toggleLike(): void {
    if (this.item.likedByIds.indexOf(this.userId) >= 0) {
      this.newsService.removeLike({id: this.item.id}).subscribe();
      this.item.likesCount--;
      this.item.likedByIds = this.item.likedByIds.filter(l => l !== this.userId);
    } else {
      this.newsService.addLike({id: this.item.id}).subscribe();
      this.item.likesCount++;
      this.item.likedByIds.push(this.userId);
    }
  }

  public hasLike(): boolean {
    return this.item.likedByIds.indexOf(this.userId) >= 0;
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)') || null;
  }
}
