import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { News } from './news';
import { NewsService } from './news.service';
import { Observable } from 'rxjs';
import { IListItemsPage } from '../list-items-service';

@Injectable()
export class NewsResolver implements Resolve<IListItemsPage<News>> {

  constructor(private newsService: NewsService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IListItemsPage<News>> {
    return this.newsService.getItemsPaged({ filter: `slNewsPublicationDate le datetime'${new Date().toISOString()}'` });
  }
}
