import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { IListItemsPage } from 'src/app/services/list-items-service';
import { NewsRubric } from 'src/app/services/news-rubrics/news-rubric';
import { News } from 'src/app/services/news/news';
import { NewsService } from 'src/app/services/news/news.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { NewsViewsService } from 'src/app/services/news-views/news-views.service';
import { NewsCommentsService } from 'src/app/services/news-comments/news-comments.service';
import { PortalService } from 'src/app/services/portal/portal.service';
import { NewsComment } from 'src/app/services/news-comments/newsComment';
import { NewsView } from 'src/app/services/news-views/newsView';

@Component({
  selector: 'app-news-page',
  templateUrl: './news-page.component.html',
  styleUrls: ['./news-page.component.scss']
})
export class NewsPageComponent implements OnInit {
  public rubrics: NewsRubric[] = [];
  private top = 10;
  private pages: IListItemsPage<News>[] = [];
  private index = 0;
  public news: News[] = [];
  public hasPrev = false;
  public hasNext = false;
  public rubricId = null;
  public userId: number = null;

  constructor(
    private newsService: NewsService,
    private route: ActivatedRoute,
    private sidebarService: SidebarService,
    private newsViewsService: NewsViewsService,
    private newsCommentsService: NewsCommentsService,
    private sharepointService: PortalService
  ) {
    this.sidebarService.handleSetSettings({showSidebar: false, bgColor: '#fff'});
  }

  ngOnInit() {
    this.rubrics = this.route.snapshot.data.newsRubrics;
    this.pages.push(this.route.snapshot.data.news);
    this.news = this.route.snapshot.data.news.items;
    this.loadViewsAndComments(this.news);
    this.hasNext = this.route.snapshot.data.news.hasNext;
    this.userId = this.route.snapshot.data.userId;
  }

  public getNextNews(): void {
    if (this.hasNext) {
      if (this.index === (this.pages.length - 1)) {
        this.pages[this.index]
          .getNext()
          .subscribe((page: IListItemsPage<News>) => {
            this.index++;
            this.news = page.items;
            this.hasNext = page.hasNext;
            this.hasPrev = true;
            this.pages.push(page);
            this.loadViewsAndComments(this.news);
          });
      } else {
        this.index++;
        this.news = this.pages[this.index].items;
        this.hasNext = this.pages[this.index].hasNext;
        this.hasPrev = true;
      }
    }
  }

  public getPrevNews(): void {
    if (this.hasPrev) {
      this.index--;
      this.news = this.pages[this.index].items;
      this.hasNext = true;
      this.hasPrev = this.index > 0;
    }
  }

  public filterByRubricId(id?: number) {
    this.rubricId = id;
    this.filterNews();
  }

  public filterNews() {
    const filter =
      (this.rubricId && `slNewsRubricLookup/Id eq ${this.rubricId} and `) || '';
    this.index = 0;
    this.news = [];
    this.pages = [];
    this.hasPrev = false;
    this.hasNext = false;
    this.newsService
      .getItemsPaged({
        filter: `${filter}slNewsPublicationDate le datetime'${new Date().toISOString()}'`,
        top: this.top
      })
      .subscribe((page: IListItemsPage<News>) => {
        this.pages.push(page);
        this.news = page.items;
        this.hasNext = page.hasNext;
        this.loadViewsAndComments(this.news);
      });
  }

  private loadViewsAndComments(news: News[]) {
    let filter = '';
    if (news.length > 0) {
      for (const n of news) {
        filter += `slLookupNews/Id eq ${n.id} or `;
      }
      filter = filter.slice(0, -4);
      this.newsCommentsService.getItems({filter: filter, top: 5000})
        .subscribe((comments: NewsComment[]) => {
          for (const n of this.news) {
            n.commentsCount = comments.filter(c => c.newsLookupId === n.id).length;
          }
        });
      this.newsViewsService.getItems({filter: filter, top: 5000})
        .subscribe((views: NewsView[]) => {
          for (const n of this.news) {
            const view = views.filter(c => c.newsLookupId === n.id);
            if (view && view.length > 0) {
              n.viewsCount = view[0].count;
            } else {
              n.viewsCount = 0;
            }
          }
        });
    }
  }

  public toggleLike(item: News): void {
    if (item.likedByIds.indexOf(this.userId) >= 0) {
      this.newsService.removeLike({id: item.id}).subscribe();
      item.likedByIds = item.likedByIds.filter(l => l !== this.userId);
      item.likesCount--;
    } else {
      this.newsService.addLike({id: item.id}).subscribe();
      item.likedByIds.push(this.userId);
      item.likesCount++;
    }
  }

  public hasLike(item: News): boolean {
    return item.likedByIds.indexOf(this.userId) >= 0;
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)') || null;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slNews/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slNews/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.newsService.recycleItem(item)
      .subscribe(() => this.filterByRubricId());
  }
}
